// Shared, non-themeable variables

// Navbar background, links, text, etc. colors
$navbar-inverse-bg: $brand-primary;
$navbar-inverse-color: rgba(255, 255, 255, .8);
$navbar-inverse-link-color: rgba(255, 255, 255, .8);
$navbar-inverse-link-hover-color: rgba(255, 255, 255, 1);
$navbar-inverse-link-active-color: rgba(255, 255, 255, 1);
$navbar-inverse-link-active-bg: darken($navbar-inverse-bg, 5%);
// Controls navbar collapse/expand. We're not doing responsive, so make it zero.
$grid-float-breakpoint: 0;

// Custom font family
$font-family-sans-serif: 'Fira Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;

// Colors
$brand-success: #548b2a;
$brand-warning: #f1d700;
$brand-danger: #bf2424;

$state-info-text: darken($brand-info, 5%);
$state-info-bg: lighten($brand-info, 48%);
$state-info-border: darken($state-info-bg, 5%);

$state-success-text: darken($brand-success, 5%);
$state-success-bg: #e2f0d7;
$state-success-border: darken($state-success-bg, 5%);

$state-danger-text: darken($brand-danger, 5%);
$state-danger-bg: #f8e2e2;
$state-danger-border: darken($state-danger-bg, 5%);

$state-warning-text: darken($brand-warning, 25%);
$state-warning-bg: #ffffe0;
$state-warning-border: darken(desaturate($state-warning-bg, 40%), 10%);

// Bring in bootstrap
@import "bootstrap/index";

// Bring in custom insight components
@import "insight/index";
