//
// Alerts
// --------------------------------------------------


// Base styles
// -------------------------

.alert {
  padding: $alert-padding;
  margin-bottom: $line-height-computed;
  border: 1px solid transparent;
  border-radius: $alert-border-radius;

  // Headings for larger alerts
  h4 {
    margin-top: 0;
    // Specified for the h4 to prevent conflicts of changing $headings-color
    color: inherit;
  }

  // Improve alignment and spacing of inner content
  > p,
  > ul {
    margin-bottom: 0;
  }

  > p + p {
    margin-top: 5px;
  }
}

// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissable, // The misspelled .alert-dismissable was deprecated in 3.2.0.
.alert-dismissible {
  padding-right: ($alert-padding + 20);
  position: relative; // for showing the .alert-timer

  // Adjust close link position
  .close {
    position: relative;
    top: -2px;
    right: -21px;
    color: inherit;
  }
}

// Alternate styles
//
// Generate contextual modifier classes for colorizing the alert.

.alert-success {
  @include alert-variant($alert-success-bg, $alert-success-border, $alert-success-text);
}

.alert-info {
  @include alert-variant($alert-info-bg, $alert-info-border, $alert-info-text);
}

.alert-warning {
  @include alert-variant($alert-warning-bg, $alert-warning-border, $alert-warning-text);
}

.alert-danger {
  @include alert-variant($alert-danger-bg, $alert-danger-border, $alert-danger-text);
}

// Icon Alert
//
// For showing a consistently-sized icon to the left in the alert

.alert-has-icon {
  padding-left: ($alert-padding * 3) + 24px;
  position: relative;

  .svg-icon {
    width: 24px !important;
    height: 24px !important;
    position: absolute;
    left: $alert-padding;
    top: ($alert-padding - 2);
    padding-right: $alert-padding;
    border-right: 1px solid rgba(0, 0, 0, .1);
    box-sizing: content-box; // override since we need padding on the outside
  }
}

// Timed Alert
//
// A child element of the alert which shows a timer and will auto dismiss

.alert-timer {
  width: 0;
  height: 2px;
  background-color: rgba(0, 0, 0, .25);
  position: absolute;
  bottom: -1px; // offset 1px border
  left: 0;
  animation-name: alert-count-down;
  animation-timing-function: linear;
  // animation timing gets applied in the component
}

@keyframes alert-count-down {
  from {
    width: 100%;
  }

  to {
    width: 0%;
  }
}

// Global Alert
//
// An alert that shows fixed in the center of the screen

.alert-global {
  width: 600px;
  position: fixed;
  top: 65px;
  left: 50%;
  margin-left: -300px;
  z-index: $zindex-alert-global;
  border-radius: $alert-border-radius;
  box-shadow: 0 7px 15px rgba(0, 0, 0, 0.25), 0 1px 3px rgba(0, 0, 0, .2);
}
